<template>
  <div class="user-card">
    <van-cell-group inset>
      <van-cell icon='manager-o' title="" :value="loginName" size="large" />
      <van-cell icon='phone-o' title="" :value="nickName" size="large" />
      <van-cell icon='home-o' title="" :value="introduceSign" size="large" />
      <van-cell icon='home-o' title="" is-link :label="address" size="large" to="/user/address" />
      <van-cell icon='home-o' title="" is-link size="large" to="/user/order" />
    </van-cell-group>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
export default {
  data() {
    return {
    }
  },
  props: {
  },
  computed: {
    ...mapState('user', ['loginName', 'nickName', 'introduceSign', 'addressInfo', 'defaultAddress', 'addressList']),
    address() {//
      if (this.defaultAddress == '') {
        return ''
      } else if (this.defaultAddress.userName !== undefined) {
        const address = `${this?.defaultAddress?.userName} ${this?.defaultAddress?.userPhone}
      ${this?.defaultAddress?.provinceName}${this?.defaultAddress?.cityName}${this?.defaultAddress?.regionName}${this?.defaultAddress?.detailAddress}`
        return address
      } else {
        return ''
      }

    }
  },
  async mounted() {
    if (!this.loginName) {//tokentoken
      console.log('---', localStorage.getItem('token'))
      if (localStorage.getItem('token')) {
        console.log('token---')
        await this.getUserInfo()//Vuex
        if (!this.defaultAddress.addressId) {//
          await this.getDefaultAddress()//Vuex
          await this.getAddressList()//Vuex
        }
      } else {
        console.log('token---')
      }
    }
  },
  methods: {
    ...mapActions('user', ['getUserInfo', 'getDefaultAddress', 'getAddressList'])
  }
}
</script>

<style scoped>
.user-card {
  padding: .4rem .0667rem;
}
</style>
