//Vue
import Vue from 'vue'
//vue-router
import vueRouter from 'vue-router'
Vue.use(vueRouter)

//
import HomePage from './pages/home/homePage.vue'
import CategoryPage from './pages/category/categoryPage.vue'
import CartPage from './pages/cart/cartPage.vue'
import UserPage from './pages/user/userPage.vue'
import SearchPage from './pages/search/searchPage.vue'
import LoginPage from './pages/login/loginPage.vue'
import RegistryPage from './pages/registry/registryPage.vue'
import AddressCard from './pages/user/AddressCard.vue'
import OrderCard from './pages/user/OrderCard.vue'
import DetailPage from './pages/detail/detailPage.vue'
// const HomePage = () => import('./pages/home/homePage.vue')//
// const CategoryPage = () => import('./pages/category/categoryPage.vue')//
// const CartPage = () => import('./pages/category/cartPage.vue')//
// const UserPage = () => import('./pages/category/userPage.vue')//
// const SearchPage = () => import('./pages/category/searchPage.vue')//
// const LoginPage = () => import('./pages/category/loginPage.vue')//
// const RegistryPage = () => import('./pages/category/registryPage.vue')//
// const AddressCard = () => import('./pages/category/AddressCard.vue')//
// const OrderCard = () => import('./pages/category/OrderCard.vue')//
// const DetailPage = () => import('./pages/category/detailPage.vue')//
Vue.component(HomePage)
Vue.component(CategoryPage)
Vue.component(CartPage)
Vue.component(UserPage)
Vue.component(LoginPage)
Vue.component(DetailPage)
//
import store from './store/index.js'

//
const router = new vueRouter({
  mode: 'hash',
  routes: [
    //
    { path: '/home', components: { mainPage: HomePage }, alias: '/', meta: { text: '', id: '1' }, name: 'HomePage' },
    //
    { path: '/category', components: { mainPage: CategoryPage }, meta: { text: '', id: '2' }, name: 'CategoryPage' },
    //
    { path: '/cart', components: { mainPage: CartPage }, meta: { text: '', id: '3' }, name: 'CartPage' },
    //
    {
      path: '/user', components: { mainPage: UserPage }, meta: { text: '', id: '4' }, name: 'UserPage', children: [
        { path: '/user/address', components: { detailCard: AddressCard } },
        { path: '/user/order', components: { detailCard: OrderCard } }
      ]
    },
    //
    { path: '/search', components: { mainPage: SearchPage }, meta: { text: '', id: '5' }, name: 'SearchPage' },
    //
    { path: '/login', components: { mainPage: LoginPage }, meta: { text: '', id: '6' }, name: 'LoginPage' },
    //
    { path: '/registry', components: { mainPage: RegistryPage }, meta: { text: '', id: '7' }, name: 'RegistryPage' },
    //
    { path: '/goods/:id', components: { mainPage: DetailPage }, meta: { text: '', id: '8' }, name: 'DetailPage' },
    //
    { path: '/*', redirect: '/' }
  ],
  // scrollBehavior(to, from, savedPosition) {//
  //   if (savedPosition) {
  //     return savedPosition
  //   } else {
  //     return { x: 0, y: 0 }
  //   }
  // }
})
//
router.beforeEach((to, from, next) => {
  if (to.path == '/cart' || to.path == '/user' || to.path == '/category' || to.path == '/search' || /^good/.test(to.path.slice(1))) {//
    if (localStorage.getItem('token')) {//token
      next()
    } else {
      router.push('/login')
    }
  } else {
    next()
  }
  if (to.path == '/login') {//
    if (localStorage.getItem('token')) {
      next()
    }
  }
  if (to.path == '/search') {//
    store.dispatch('goods/getSearchResult', { keyword: to.query.rec })
    next()
  }
  if (/^goods/.test(to.path.slice(1))) {//
    store.dispatch('goods/getGoodsDetail', to.params.id)
    store.dispatch('cart/getCartList')//
    next()
  }
})
//
export default router