//
import { fetchAddCart } from "@/utils/api"
//
import { fetchCartList } from "@/utils/api"
//
import { fetchEditCart } from "@/utils/api"
//
import { fetchDeleteCart } from "@/utils/api"
import { Toast } from "vant"
export default {
  namespaced: true,
  state: {
    cartList: [],//
  },
  mutations: {
    //
    setCartList(state, val) {
      state.cartList = val
    }
  },
  actions: {
    //
    getAddCart(store, value) {
      return fetchAddCart(value).then(res => {
        if (res.status == 200) {
          if (res.data.resultCode == 200) {
            console.log('---')
            Toast('~')
            //
            store.dispatch('getCartList')
          } else if (res.data.resultCode == 500) {//
            if (value.operation == 'add') {
              Toast('\n~')
            }

          } else {
            console.log('---', res)
          }
        } else {
          console.log('---', res)
        }
      })
    },
    //
    getCartList(store) {
      return fetchCartList().then(res => {
        if (res.status == 200) {
          if (res.data.resultCode == 200) {
            console.log('---')
            store.commit('setCartList', res.data.data)//
          } else {
            console.log('---', res)
          }
        } else {
          console.log('---', res)
        }
      })
    },
    //
    getEditCart(store, value) {
      return fetchEditCart(value).then(res => {
        if (res.status == 200) {
          if (res.data.resultCode == 200) {
            console.log('---')
            store.dispatch('getCartList')//
          } else if (res.data.resultCode == 416) {//
            console.log('res.data.message---')
            Toast('~')
            store.dispatch('user/getLogout')
          } else {
            console.log('---', res)
          }
        } else {
          console.log('---', res)
        }
      })
    },
    //
    getDeleteCart(store, cartId) {
      return fetchDeleteCart(cartId).then(res => {
        if (res.status == 200) {
          if (res.data.resultCode == 200) {
            console.log('---')
            store.dispatch('getCartList')//
          } else {
            console.log('---', res)
          }
        } else {
          console.log('---', res)
        }
      })
    }

  }
}