<template>
  <div class="grid">
    <van-swipe :loop="false" lass="my-swipe" indicator-color="#d81e06">
      <van-swipe-item v-for="num in pageCount" :key="num" style="padding-bottom:.5rem">
        <van-grid :column-num="5">
          <van-grid-item v-for="item in gridData.slice(10 * (num - 1), 10 * (num - 1) + 10)" :key="item.id"
            :text="item.text" @click="wait">
            <template #icon><img :src="item.icon" /></template>
          </van-grid-item>
        </van-grid>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data() {
    return {

    }
  },
  props: {
    gridData: {
      type: Array,
      default: () => [
        { id: 1, text: '', icon: require('@/assets/grid-icon/market.png') },
        { id: 2, text: '', icon: require('@/assets/grid-icon/digital.png') },
        { id: 3, text: '', icon: require('@/assets/grid-icon/veg.png') },
        { id: 4, text: '', icon: require('@/assets/grid-icon/deliver.png') },
        { id: 5, text: '', icon: require('@/assets/grid-icon/topup.png') },
        { id: 6, text: '', icon: require('@/assets/grid-icon/nearby.png') },
        { id: 7, text: '', icon: require('@/assets/grid-icon/coupon.png') },
        { id: 8, text: '', icon: require('@/assets/grid-icon/vip.png') },
        { id: 9, text: '', icon: require('@/assets/grid-icon/import.png') },
        { id: 10, text: '', icon: require('@/assets/grid-icon/auction.png') },
        { id: 11, text: '', icon: require('@/assets/grid-icon/womart.png') },
        { id: 12, text: '', icon: require('@/assets/grid-icon/makeup.png') },
        { id: 13, text: '', icon: require('@/assets/grid-icon/journey.png') },
        { id: 14, text: '', icon: require('@/assets/grid-icon/all.png') }
      ]
    }
  },
  computed: {
    pageCount() {
      return Math.ceil(this.gridData.length / 10)
    }
  },
  methods: {
    //
    wait() {
      Toast('')
    }
  }
}
</script>

<style scoped>
/*  */
.grid van-swipe-item {
  background-color: #f6f6f6;
}

/*  */
.grid img {
  width: 1.12rem;
}

/*  */
.grid .van-grid-item__text {
  font-size: .16rem;
}
</style>

<style>
/*  */
.grid .van-swipe__track,
.grid .van-grid-item__content {
  background-color: #f6f6f6;
}
</style>
