<template>
  <div id="CartSubmit">
    <div class="SelectAddress">
      <h1></h1>
      <van-dropdown-menu direction="up" active-color="#1989fa">
        <van-dropdown-item v-model="selectedAddress" :options="list" />
      </van-dropdown-menu>
    </div>
    <van-submit-bar :price="sum" button-text="" @submit="onSubmit">
      <van-checkbox v-model="checked"></van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import { Toast } from 'vant';
import { mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      selectedAddress: ''//
    }
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapState('user', ['addressList']),
    //
    sum() {
      return this.cartList.reduce((sum, item1) => {//resultcateItemId
        return this.$parent.result.some(item2 => { return item1.cartItemId == item2 }) ? sum + item1.goodsCount * item1.sellingPrice * 100 : sum + 0
      }, 0)
    },
    checked: {
      get() {//length
        if (this.cartList.length) {
          return this.$parent.result.length == this.cartList.length
        } else {
          return false
        }

      },
      set(val) {//cartPagecheckboxGroup/
        if (this.cartList.length) {
          this.$parent.$refs.checkboxGroup.toggleAll(val)
        } else {
          Toast('')
        }

      }
    },
    //
    list() {
      const list = []
      if (this.addressList) {
        Object.values(this.addressList).forEach(({ addressId, cityName, defaultFlag, detailAddress, provinceName, regionName, userName, userPhone }) => {
          list.push({
            text: `${userName}${userPhone}${provinceName}${cityName}${regionName}${detailAddress}`,
            value: addressId
          })
          if (defaultFlag == '0001') { this.selectedAddress = addressId }//
        })
      }
      return list
    },
  },
  created() {
  },
  mounted() {
  },
  methods: {
    ...mapActions('order', ['getSaveOrder']),
    //
    async onSubmit() {
      if (this.$parent.result.length == 0) {//
        Toast('')
      } else {
        console.log({
          addressId: this.selectedAddress,
          cartItemIds: this.$parent.result
        })
        await this.getSaveOrder({//
          addressId: this.selectedAddress,
          cartItemIds: this.$parent.result
        })
        this.$router.push('/user/order')
      }
    }

  }


}
</script>

<style lang="scss" >
#CartSubmit {
  width: 100vw;
  height: fit-content;
  position: fixed;
  bottom: 50px;

  // 
  .SelectAddress {
    position: absolute;
    bottom: 1.3333rem;
    height: fit-content;
    width: 100%;
    color: brown;

    h1 {
      text-indent: .3rem;
      font-size: .3rem;
      line-height: .3rem;
    }

    .van-dropdown-menu__title {
      padding: 0 .6rem;
      font-size: .4rem;
      line-height: .4rem;
    }


  }

  //
  .van-submit-bar {
    position: absolute;

    bottom: 0;

    .van-submit-bar__bar {
      height: 1.3333rem;
      font-size: .1867rem;

      //
      .van-checkbox__icon {
        font-size: .5333rem;
      }

      //
      .van-checkbox__label {
        line-height: .3rem;
      }

      //
      .van-submit-bar__text {

        //
        .van-submit-bar__price {
          font-size: .4rem;

          //
          .van-submit-bar__price--integer {
            font-size: .5rem;
          }
        }
      }

      //
      .van-button--normal {
        font-size: .4rem;
        line-height: .4rem;
        padding: .3rem .5rem;
        height: fit-content;
        width: fit-content;
      }



    }
  }

}
</style>
