<template>
  <div id="categoryPage">
    <div class="search-box">
      <CateSearch :maskshow="maskshow"></CateSearch>
    </div>
    <SearchMask :maskshow="maskshow"></SearchMask>
    <div class="cate-box">
      <SideBar ref="sidebar"></SideBar>
      <CateContent ref="catecontent"></CateContent>
    </div>
    <JdTabbar></JdTabbar>
  </div>
</template>

<script>
import JdTabbar from '@/components/JdTabbar.vue';
import CateSearch from '@/components/CateSearch.vue'
import SideBar from '@/components/SideBar.vue';
import CateContent from '@/components/CateContent.vue';
import SearchMask from '@/components/SearchMask.vue';
export default {
  name: 'CategoryPage',
  data() {
    return {
      maskshow: false,
      scrollMark1: 0,//sidebar
      scrollMark2: 0,//content
      contentRecord: ''//categoryId
    }
  },
  computed: {
  },
  props: {
    ifSkipShow: { Boolean, default: true }
  },
  components: { JdTabbar, CateSearch, SideBar, CateContent, SearchMask },
  beforeRouteLeave(to, from, next) {//
    this.scrollMark1 = this.$refs.sidebar.scrollMark//sidebar
    this.scrollMark2 = this.$refs.catecontent.scrollMark//content
    this.contentRecord = this.$route.query.id//ID
    console.log('categoryPage---')
    next()
  },
  activated() {
    if (this.contentRecord != '') {//
      this.$router.push(`/category?id=${this.contentRecord}`)
    } else {//
      this.$router.push('/category?id=cate30')
    }
    console.log('categoryPage---')
  }
}
</script>
<style lang="scss">
#categoryPage {
  height: 100%;

  // 
  .search-box {
    width: 100%;
    position: relative;
    top: 0;
    z-index: 3;
  }

  // 
  .cate-box {
    position: relative;
    margin: 0;
    height: 16.5867rem;
    overflow: hidden;
    background-color: #f6f6f6;
  }

}
</style>
