<template>
  <div class="UserTitle">
    <van-button icon="arrow-left" type="primary" color="white" icon-color="black" @click="back" />
    <div v-text="title" class="text"></div>
    <van-popover v-model="showPopover" trigger="click" :actions="actions" @select="onSelect" theme="dark"
      placement="bottom-end">
      <template #reference>
        <van-button icon="ellipsis" type="primary" color="white" class="ellipsis" />
      </template>
    </van-popover>
    <van-dialog v-model="show" title="" show-cancel-button @confirm="submit" @cancel="clean">
      <van-field v-model="uname" label="" placeholder="" :rules="[{ required: true, message: '' }]" />
      <van-field v-model="sign" label="" placeholder="" :rules="[{ required: true, message: '' }]" />
      <van-field v-model="password" type="password" label="" placeholder=""
        :rules="[{ required: true, message: '' }]" />
    </van-dialog>
  </div>
</template>

<script>
import { Dialog } from 'vant';
import { mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      actions: [{ text: '', icon: 'more-o' }],
      showPopover: false,
      show: false,
      uname: this.nickName,
      sign: this.introduceSign,
      password: ''
    }
  },
  computed: {
    ...mapState('user', ['nickName', 'introduceSign']),
    values() {//
      return [this.uname, this.sign, this.password]
    }
  }
  ,
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  methods: {
    ...mapActions('user', ['getEditInfo']),
    //
    onSelect(action) {
      switch (action.text) {//
        case "":
          this.show = true;
          break;
      }
    },
    //
    back() {
      if (this.maskshow) {
        this.$parent.maskshow = false;
      } else {
        this.$router.replace('/home')
      }
    },
    //
    async submit() {
      if (this.values.every((item) => {//
        return item != ''
      })) {
        await this.getEditInfo(this.values)//
        this.clean()//
      } else {
        Dialog.confirm({ message: '' })//
        this.clean()//
      }
    },
    //
    clean() {
      this.uname = this.nickName,
        this.sign = this.introduceSign,
        this.password = ''
    }
  }


}
</script>

<style lang="scss">
/*  */
.UserTitle {
  height: 1.1733rem;
  border-bottom: 1.5px solid #f6f6f6;
  display: flex;
  background-color: white;

  // 
  &>.van-button {
    height: 100%;
    flex: 1;
    padding: 0 .3rem;

    .van-icon {
      color: gray;
      font-size: .4rem;
      line-height: .6533rem;
    }
  }

  //
  .van-popover__wrapper {
    height: 100%;
    flex: 1;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    .van-button {
      flex: 1;
      width: 100%;
      height: 100%;

      &::before {
        border: 0;
      }

      .van-icon {
        font-size: .4rem;
        color: gray;
        line-height: .6533rem;
      }
    }
  }

  /*  */
  .text {
    height: 100%;
    flex: 15;
    line-height: 1.1733rem;
    font-size: .5rem;
    text-align: center;
  }


}

//(app)
.van-popover__action {
  width: 4rem;
}
</style>
