<template>
  <div class="OrderCard">
    <van-tabs v-model="active">
      <van-tab v-for="(item1, index) in status" :key="index" :title="item1">
        <div v-for="item2 in currentList" :key="item2.orderId" @click=toOrderDetail(item2)>
          <h3 class="orderNo" v-text="`${item2.orderNo}`"></h3>
          <van-card v-for="item3 in item2.newBeeMallOrderItemVOS" :key="item3.goodsId" :num="item3.goodsCount"
            :price="item3.sellingPrice.toFixed(2)" :title="item3.goodsName"
            :thumb="`http://192.168.1.3:28019${item3.goodsCoverImg}`">
            <template #tags>
              <van-tag plain type="danger">{{ item2.orderStatusString }}</van-tag>
            </template>
            <template #footer>
              <span v-text="`${item3.goodsCount * item3.sellingPrice.toFixed(2)}`"></span>
            </template>
          </van-card>
          <h3 class="totalPrice" v-text="`${item2.totalPrice.toFixed(2)}`" style="color:brown"></h3>
          <hr>
        </div>
        <van-empty v-show="!currentList?.length" :description="`${item1}`" />
      </van-tab>
    </van-tabs>
    <div v-if="ifOperate" class="detail">
      <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
      <van-steps :active="step" active-color="brown">
        <van-step></van-step>
        <van-step></van-step>
        <van-step></van-step>
        <van-step></van-step>
        <van-step></van-step>
      </van-steps>
      <h3 v-show="step == -1" style="color:black;text-align: center; font-size: .3rem;margin-left: .3rem;"></h3>
      <van-card v-for="item in orderDetail.newBeeMallOrderItemVOS" :key="item.goodsId" :num="item.goodsCount"
        :price="item.sellingPrice.toFixed(2)" :title="item.goodsName"
        :thumb="`http://192.168.1.3:28019${item.goodsCoverImg}`" @click="toGoodsDetail(item)">
        <template #footer>
          <span v-text="`${item.goodsCount * item.sellingPrice.toFixed(2)}`"></span>
        </template>
      </van-card>
      <h3 v-text="`${orderDetail.totalPrice.toFixed(2)}`" style="color:brown"></h3>
      <h3 v-text="`${orderDetail.orderNo}`" style="color:brown"></h3>
      <h3 v-text="`${orderDetail.createTime}`" style="color:brown"></h3>
      <hr>
      <van-button v-show="step == 0" class="option" round type="info" @click="toPay"></van-button>
      <van-button v-show="step <= 2 && step != -1" class="option" round type="default" @click="toCancel"></van-button>
      <van-button v-show="step == 3" class="option" round type="warning" @click="toFinish"></van-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      status: ['', '', '', '', '', '', ''],//
      ifOperate: false, //
      active: 0,//
    };
  },
  computed: {
    ...mapState('order', ['orderList', 'orderNeedPay', 'orderNeedConfirm', 'orderNeedDispatch', 'orderDispatching', 'orderFinished', 'orderClosed', 'orderDetail']),
    step() {//0
      return this.orderDetail.orderStatus//
    },
    currentList() {
      let result = []
      switch (this.active) {
        case 0: result = this.orderList; break
        case 1: result = this.orderNeedPay; break
        case 2: result = this.orderNeedConfirm; break
        case 3: result = this.orderNeedDispatch; break
        case 4: result = this.orderDispatching; break
        case 5: result = this.orderFinished; break
        case 6: result = this.orderClosed
      }
      return result
    }
  },
  async mounted() {
    await this.getOrderList()//
  },
  methods: {
    ...mapActions('order', ['getOrderList', 'getOrderDetail', 'getCancelOrder', 'getOrderFinished', 'getPaySuccess']),
    //
    async toOrderDetail(item2) {
      await this.getOrderDetail(item2.orderNo)//
      this.ifOperate = true
    },
    //
    onClickLeft() {
      this.ifOperate = false;//
    },
    //
    toGoodsDetail(item) {
      this.$router.push({
        name: 'DetailPage',
        params: { id: item.goodsId }
      })
    },
    //
    async toPay() {
      await this.getPaySuccess({ orderNo: this.orderDetail.orderNo, payType: 1 })//
      await this.getOrderDetail(this.orderDetail.orderNo)//
      //
      this.ifOperate = false;
      this.ifOperate = true;
    },
    //
    toCancel() {
      this.getCancelOrder(this.orderDetail.orderNo)//
      this.getOrderDetail(this.orderDetail.orderNo)//
      this.ifOperate = false;//
    },
    //
    toFinish() {
      this.getOrderFinished(this.orderDetail.orderNo)//
      this.getOrderDetail(this.orderDetail.orderNo)//
    }
  }
}

</script>


<style lang="scss">
.OrderCard {
  height: 100%;
  overflow-y: scroll;

  &::-webkit-scroll {
    width: 0;
  }


  //
  &>.van-tabs--line {
    height: 100%;

    &>.van-tabs__content {
      height: calc(100% - 44px);
      overflow-y: scroll;

      .van-tab__pane {
        height: 100%;

        // 
        .orderNo {
          font-size: .2rem;
          margin-left: .3rem
        }

        //
        .totalPrice {
          text-align: right;
          font-size: .2rem;
          margin-right: .3rem;
        }

      }

    }
  }



  //
  .detail {
    box-sizing: border-box;
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: white;
    overflow-y: scroll;
    overflow-x: hidden;


    &::-webkit-scrollbar {
      width: 0
    }

    //
    h3 {
      text-align: right;
      font-size: .2rem;
      margin-right: .3rem;
    }

    //
    .option {
      float: right;
      margin-right: .3rem
    }
  }
}
</style>
