<template>
  <div id="detailPage">
    <van-sticky><van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" /></van-sticky>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for=" (item, index) in goodsCarouselList" :key="index">
          <img v-lazy="`http://192.168.1.3:28019${item}`" class="carousel" />
        </van-swipe-item>
      </van-swipe>
      <h1 v-text="goodsDetail.goodsName" class="goodsName"></h1>
      <h2 v-text="goodsDetail.goodsIntro" class="goodsIntro"></h2>
      <div class="sellingPrice" v-text="`${goodsDetail?.sellingPrice?.toFixed(2)}`"></div>
      <div class="originalPrice" v-text="`${goodsDetail?.originalPrice?.toFixed(2)}`"></div>
    </van-pull-refresh>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="" @click="onClickIcon" v-login />
      <van-goods-action-icon icon="cart-o" text="" to="/cart" :badge="this.cartList?.length" v-login />
      <van-goods-action-button type="warning" text="" @click="addCart" v-login />
      <van-goods-action-button type="danger" text="" @click="buyNow" v-login to="/cart" />
    </van-goods-action>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
export default {
  name: 'DetailPage',
  data() {
    return {
      isLoading: false
    }
  },
  computed: {
    ...mapState('goods', ['goodsDetail']),
    ...mapState('cart', ['cartList']),
    //
    goodsCarouselList() {
      return this.goodsDetail.goodsCarouselList?.map(item => {
        if (item[0] != '/') {
          return item.slice(item.indexOf('/', 7))
        } else {
          return item
        }

      })
    }
  },
  methods: {
    ...mapActions('goods', ['getGoodsDetail']),
    ...mapActions('cart', ['getAddCart']),
    ...mapMutations('goods', ['setGoodsDetail']),
    //
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false
      }, 1000)
      this.isLoading = true
      this.$router.go(0)//
    },
    //
    onClickIcon() {

    },
    //
    addCart() {
      this.getAddCart({ goodsCount: 1, goodsId: this.$route.params.id, operation: 'add' })
    },
    //
    buyNow() {
      this.getAddCart({ goodsCount: 1, goodsId: this.$route.params.id, operation: 'buy' })
    }
  },
  //vuex
  beforeRouteLeave(to, from, next) {
    this.setGoodsDetail({})
    next()
  }

}
</script>

<style lang="scss" scoped>
#detailPage {
  .van-pull-refresh {

    //
    .van-swipe {
      height: 100vw;

      .van-swipe-item {
        color: #fff;
        font-size: 20px;
        line-height: 150px;
        text-align: center;
        background-color: gray;


        .carousel {
          width: 100%;
        }
      }
    }

    //
    .goodsName {
      text-indent: .2667rem;
      font-size: .5rem;
    }

    //
    .goodsIntro {
      text-indent: .2667rem;
      font-size: 0.4rem;
    }

    //
    .sellingPrice {
      text-indent: .2667rem;
      color: red;
      font-size: .3rem;
    }

    //
    .originalPrice {
      text-indent: .2667rem;
      color: gray;
      font-size: .2rem;
      text-decoration: line-through;
    }


  }
}
</style>
