<template>
  <div class="AddressCard">
    <van-address-list v-model="chosenAddressId" :list="list" default-tag-text="" switchable @add="onAdd" @edit="onEdit"
      @select="onSelect">
      <van-empty v-show="!list" description="" />
    </van-address-list>
    <div v-if="ifOperate" class="edit">
      <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
      <van-address-edit :area-list="areaList" show-delete :area-columns-placeholder="['', '', '']"
        :address-info="init" @save="onSave" @delete="onDelete" />
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import { areaList } from '@vant/area-data'//vant
import { Toast } from 'vant';

export default {
  data() {
    return {
      areaList,
      ifOperate: false, //
      changeOrAdd: '',//
      chosenAddressId: ''//addressId
    };
  },
  computed: {
    ...mapState("user", ["addressList", 'defaultAddress', 'addressDetail']),
    //
    list() {
      const list = []
      if (this.addressList) {
        Object.values(this.addressList).forEach(({ addressId, cityName, defaultFlag, detailAddress, provinceName, regionName, userName, userPhone }) => {
          list.push({
            id: addressId,
            name: userName,
            tel: userPhone,
            address: `${provinceName}${cityName}${regionName}${detailAddress}`,
            isDefault: defaultFlag == '0001' ? true : false
          })
          if (defaultFlag == '0001') { this.chosenAddressId = addressId }//
        })
      }
      return list
    },
    //
    init() {
      if (this.changeOrAdd == 'add') {//
        return {}
      } else {
        //changeOrAdd
        const cityName = this.addressList[this.changeOrAdd].cityName
        const areaCode = Object.keys(this.areaList.city_list)[Object.values(this.areaList.city_list).indexOf(cityName)]
        return {
          id: this.addressList[this.changeOrAdd].addressId,
          name: this.addressList[this.changeOrAdd].userName,
          tel: this.addressList[this.changeOrAdd].userPhone,
          province: this.addressList[this.changeOrAdd].provinceName,
          city: this.addressList[this.changeOrAdd].cityName,
          county: this.addressList[this.changeOrAdd].regionName,
          addressDetail: this.addressList[this.changeOrAdd].detailAddress,
          isDefault: this.addressList[this.changeOrAdd].defaultFlag == '0001' ? true : false,
          areaCode
        }
      }
    }
  },
  mounted() {
    //
    if (!this.addressList) {
      this.getAddressList();
    }
    this.chosenAddressId = this.defaultAddress.addressId
  },
  methods: {
    ...mapActions("user", ["getAddressList", 'getAddAddress', 'getEditAddress', 'getDefaultAddress', 'getDeleteAddress', 'getAddressDetail']),
    //
    onSelect(item) {
      if (item.id != this.defaultAddress.addressId) {//
        console.log('---')
        this.getAddressDetail(this.defaultAddress.addressId) //  
        this.getAddressDetail(item.id)//
        this.getDefaultAddress()//usercard
      }

    },
    //
    onAdd() {
      this.ifOperate = true; //
      this.changeOrAdd = 'add'//
    },
    //
    onEdit(item, index) {
      this.ifOperate = true; //
      this.changeOrAdd = index//,
    },
    //
    async onSave(content) {
      const data = {//
        cityName: content.city,
        defaultFlag: content.isDefault ? '0001' : '0000',
        detailAddress: content.addressDetail,
        provinceName: content.province,
        regionName: content.county,
        userName: content.name,
        userPhone: content.tel,
      }
      if (this.changeOrAdd == 'add') {//
        await this.getAddAddress(data)
      } else {//
        data.addressId = this.addressList[this.changeOrAdd].addressId
        await this.getEditAddress(data)
      }
      this.ifOperate = false//
    },
    //
    onDelete() {
      console.log('---', this.list[this.changeOrAdd])
      if (this.list[this.changeOrAdd].isDefault) {
        Toast('\n')
      } else {
        this.getDeleteAddress(this.addressList[this.changeOrAdd].addressId)
      }
      this.ifOperate = false;//
    },
    //
    onClickLeft() {
      this.ifOperate = false;//
    },
  },
  components: {}
}
</script>


<style lang="scss">
.AddressCard {
  height: 100%;

  //
  .van-address-list {
    padding: 0;

    //
    .van-address-item__value {
      padding-right: 0;
      flex: 4;

      //
      .van-address-item__name {
        font-size: .28rem
      }

      //
      .van-address-item__address {
        font-size: .28rem
      }
    }

    //
    .van-address-item__edit {
      box-sizing: border-box;
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      right: 0;
      transform: translate(0, 0);

      &::before {
        font-size: .5rem;
      }
    }

    // 
    .van-address-list__bottom {
      box-sizing: border-box;
      height: 1rem;
      padding: 0;
      z-index: 0;
      //wrapper
      background-color: transparent;

      // 
      .van-button--round {
        box-sizing: border-box;
        height: 100%;
        border-radius: .5rem;
        font-size: .2rem;
      }


    }
  }


  //
  .edit {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: white;

    //
    .van-address-edit-detail .van-cell {
      padding: 0
    }

    //
    .van-address-edit__buttons {
      box-sizing: border-box;
      padding: 0;
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: inherit;

      .van-button--round {
        box-sizing: border-box;
        height: 1rem;
        border-radius: .5rem;
        margin-bottom: 0;
        margin-top: .16rem;
        font-size: .2rem;

      }

    }
  }
}
</style>
