<template>
  <div id="CartTitle">
    <van-button icon="arrow-left" type="primary" color="white" icon-color="black" @click="back" />
    <div v-text="title"></div>
    <van-popover v-model="showPopover" trigger="click" :actions="actions" @select="onSelect" theme="dark"
      placement="bottom-end">
      <template #reference>
        <van-button icon="ellipsis" type="primary" color="white" class="ellipsis" />
      </template>
    </van-popover>
  </div>
</template>

<script>
import { Dialog } from 'vant'
import { mapActions } from 'vuex'
export default {
  data() {
    return {
      actions: [{ text: '', icon: 'more-o' }],
      showPopover: false,
    }
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    result: []//cartItemId
  },
  methods: {
    ...mapActions('cart', ['getDeleteCart']),
    onSelect(action) {
      switch (action.text) {
        case "":
          Dialog.confirm({ message: '' }).then(() => {
            this.result.forEach(async item => {
              await this.getDeleteCart(item)//
            })
            this.$emit('delete');//catePageresult
          })
      }
    },
    back() {
      if (this.maskshow) {
        this.$parent.maskshow = false;
      } else {
        this.$router.replace('/home')
      }
    }
  }


}
</script>

<style lang="scss">
/*  */
#CartTitle {
  height: 1.1733rem;
  border-bottom: 1.5px solid #f6f6f6;
  display: flex;
  background-color: white;

  // 
  &>.van-button {
    height: 100%;
    flex: 1;
    padding: 0 .3rem;

    .van-icon {
      color: gray;
      font-size: .4rem;
      line-height: .6533rem;
    }
  }

  .van-popover__wrapper {
    height: 100%;
    flex: 1;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    .van-button {
      flex: 1;
      width: 100%;
      height: 100%;

      &::before {
        border: 0;
      }

      .van-icon {
        font-size: .6533rem;
        color: gray;
        line-height: .6533rem;

      }
    }
  }

  /*  */
  &>div {
    height: 100%;
    flex: 15;
    line-height: 1.1733rem;
    font-size: .5rem;
    text-align: center;
  }
}
</style>
