<template>
  <div class="loginPage">
    <LoginTitle title=""></LoginTitle>
    <van-form @submit="onSubmit">
      <van-field v-model="username" name="" label="" placeholder=""
        :rules="[{ required: true, message: '' }]" />
      <van-field v-model="password" type="password" name="" label="" placeholder=""
        :rules="[{ required: true, message: '' }]" />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit"></van-button>
      </div>
    </van-form>
    <router-link to="/registry" class="link"></router-link>
  </div>
</template>

<script>
import LoginTitle from '@/components/LoginTitle.vue';
import { mapActions } from 'vuex';
export default {
  data() {
    return {
      username: '',
      password: '',
    }
  },
  activated() {
  },
  watch: {
  },
  created() {
  },
  computed: {

  },
  methods: {
    ...mapActions('user', ['getLogin', 'getUserInfo']),
    async onSubmit(values) {
      await this.getLogin(Object.values(values))//user
    }
  },
  components: { LoginTitle }


}
</script>

<style scoped>
.loginPage .link {
  display: inline-block;
  width: 100%;
  height: .6667rem;
  line-height: .6667rem;
  font-size: .2133rem;
  text-align: center;
}
</style>