<template>
  <div class="registryPage">
    <LoginTitle :title="''"></LoginTitle>
    <van-form @submit="onSubmit">
      <van-field v-model="username" name="" label="" placeholder=""
        :rules="[{ required: true, message: '' }]" />
      <van-field v-model="password" type="password" name="" label="" placeholder=""
        :rules="[{ required: true, message: '' }]" />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit"></van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import LoginTitle from '@/components/LoginTitle.vue';
import { mapActions } from 'vuex';
export default {
  data() {
    return {
      username: '',
      password: '',
    };
  },
  activated() {
  },
  watch: {},
  created() {
  },
  mounted() {
  },
  methods: {
    ...mapActions('user', ['getRegistry']),
    async onSubmit(values) {
      console.log('submit', values);
      await this.getRegistry(Object.values(values))
    },
  },
  components: { LoginTitle }
}
</script>

